import {delArticle} from '@/utils/api.js';
export default {
	name: 'author',
	data() {
		return {
			pendding: false,
		};
	},
	props: {
		articleInfo: {
			type: Object,
			default: () => {},
		},
		user: {
			type: Object,
			default: () => {},
		},
	},
	methods: {
		// 点赞
		favorite() {
			if (this.articleInfo.favoritedabled) return;
			this.isLogin();
			this.$emit('favoriteHandel');
		},
		// Follow
		follow() {
			if (this.articleInfo.followabled) return;
			this.isLogin();
			this.$emit('followHandel');
		},
		// 判断是否登录
		isLogin() {
			let { user } = this;
			if (!user || !user.token) {
				this.$router.push('/login');
				return;
			}
		},
        // 删除文章
        deleteArticle(){
            if(this.pendding) return;
            this.pendding = true;
            delArticle(this.articleInfo.slug).then(() => {
                this.pendding = false;
                this.$router.replace('/')
            }).catch(() => {
                this.pendding = false;
            })
        }
	},
};
